<template>
    <div>
        <el-main>
            <div class="select">
                <el-select v-model="department" clearable filterable placeholder="部门">
                    <el-option
                        v-for="item in departmentList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
                <el-select v-model="education" clearable filterable placeholder="学历">
                    <el-option v-for="item in educationList" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-input v-model="search" placeholder="姓名" clearable></el-input>
            </div>
            <el-table
                border
                tooltip-effect="dark"
                style="width: 100%"
                :header-cell-style="{background:'#f5f7fa'}"
                :data="tableData"
            >
                <el-table-column prop="wid" label="工号" min-width="100"></el-table-column>
                <el-table-column prop="name" label="姓名" min-width="100"></el-table-column>
                <el-table-column prop="sex" label="性别" min-width="100">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.sex === '0' ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.sex=='1'?"女":"男"}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="education" label="学历" min-width="100"></el-table-column>
                <el-table-column prop="department" label="部门" min-width="100"></el-table-column>
                <el-table-column min-width="130" label="操作">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="primary"
                            @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-pagination
                style="float: right;margin-top:8px;"
                background
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="7"
                :total="totalPage"
            ></el-pagination>
        </el-main>
    </div>
</template>
<script>
import router from "../router";
import moment from "moment";
import axios from "axios";
export default {
    data() {
        return {
            search: "",
            currentPage: 1,
            totalPage: 1,
            departmentList: [],
            department: "",
            educationList: [
                { value: "博士研究生" },
                { value: "本科毕业生" },
                { value: "硕士研究生" },
                { value: "专科毕业生" },
                { value: "其他" }
            ],
            education: "",
            tableData: []
        };
    },
    created: function() {
        this.getList();
        this.getDepartment();
    },
    watch: {
        department(newValue, oldValue) {
            this.getList();
        },
        search(newValue, oldValue) {
            this.getList();
        },
        education(newValue, oldValue) {
            this.getList();
        }
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/list",
                params: {
                    department: here.department,
                    education: here.education,
                    stime: "",
                    etime: "",
                    search: here.search,
                    currentPage: here.currentPage
                }
            })
                .then(function(response) {
                    let data = response.data;
                    if(data.totalPage===-1){
                        here.$message.error("您没有权限!");
                    }else{
                         here.tableData = data.data;
                         here.totalPage = data.totalPage;
                    }
                })
                .catch(function(error) {
                    here.$message.error("无法连接服务器!");
                });
        },
        getDepartment() {
            let here = this;
            axios({
                url: "/department"
            })
                .then(function(response) {
                    here.departmentList = response.data;
                })
                .catch(function(error) {
                    here.$message.error("无法连接服务器!");
                });
        },
        handleEdit(index, row) {
            let here = this;
            axios({
                url: "/edit",
                params:{
                    index:"0"
                }
            })
                .then(function(response) {
                    if (response.data.success === 1) {
                        router.push({
                            name: "tabs",
                            params: {
                                data: row,
                                index: 3
                            }
                        });
                    } else {
                        here.$message.error("您不具备该权限!");
                    }
                })
                .catch(function(error) {
                    here.$message.error("无法连接服务器!");
                });
        },
        handleCurrentChange() {
            this.getList();
        }
    }
};
</script>
<style lang="stylus" scoped>
body {
    margin: 0;
}

.bottom {
    margin-top: 13px;
    line-height: 12px;
}

.button {
    padding: 0;
    float: right;
}

.image {
    width: 100%;
    height: 100%;
    display: block;
}

.clearfix:before, .clearfix:after {
    display: table;
    content: '';
}

.clearfix:after {
    clear: both;
}

.btn {
    margin-bottom: 10px;
}

.select {
    display: inline;
    margin-bottom: 10px;
}

.select, .el-input {
    width: 156px;
    margin-bottom: 10px;
}

.el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner {
    width: 240px;
}

.bbb {
    cursor: text;
    width: 41px;
    height: 23px;
    padding: 0;
    border: 0px;
}
</style>



